/* ===================================
========= register page  */

/* login */
.login-back{
  background-color: #1F5F5B;
    background-image: linear-gradient(0deg, greenyellow 0%, #1F5F5B 100%);
}
.form-container {
    
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 80vh;
    flex-direction: column;
    background-color: #1F5F5B;
    /* background-image: linear-gradient(0deg, greenyellow 0%, #1F5F5B 100%); */
  }
  
  .form-container form {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 4px;
    padding: 20px;
    background-color: #fff;
  }
  
  
  .form-container form .title {
    text-align: center;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
  }
  
  
  .form-container form input {
    border: none;
    border-bottom: 1px solid #000;
    border-radius: 0;
  }
  .form-container form input:hover {
    border: none;
    border-bottom: 1px solid #1F5F5B;
    border-radius: 0;
  }
  
  .form-container form button {
    border: 1px solid black;
    border-radius: 7px;
    background-color: #1F5F5B;
    color: white;
    width: 250px;
    font-family: 'Poppins', sans-serif;
  }
  
  .form-container form button:hover {
    border: 1px solid black;
    font-family: 'Poppins', sans-serif;;
    background: green; /* fallback for old browsers */
    background: -webkit-linear-gradient(
      to right,
      #1F5F5B,
      yellowgreen
    ); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(
      to right,
      yellowgreen,
      #1F5F5B
    ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  
  .forgot-btn {
    background-color: #1F5F5B !important;
    border: 0 !important;
  }
  
  .forgot-btn:hover {
    color: yellowgreen !important;
  }